import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId', 'forcedLogin'])
	},
	data(e) {
		return {
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11, //层级
			afterHeaderzIndex: 10, //层级
			beforeHeaderOpacity: 1, //不透明度
			afterHeaderOpacity: 0, //不透明度
			//是否显示返回按钮



			//轮播主图数据
			swiperList: [],
			//轮播图下标
			currentSwiper: 0,
			anchorlist: [], //导航条锚点
			selectAnchor: 0, //选中锚点
			serviceClass: '', //服务弹窗css类，控制开关动画
			specClass: '', //规格弹窗css类，控制开关动画
			shareClass: '', //分享弹窗css类，控制开关动画
			// 商品信息
			mycomment: '', //评论
			scenicSpotId: '',
			name: '',
			adultTicketPrice: '',
			childrenTicketPrice: '',
			img: '',
			Describe: '',
			JDescribe: '',
			ticketName: '',
			ticketDescribe: '',
			number: 1,
			service: [{ name: '正品保证', description: '此商品官方保证为正品' }, { name: '极速退款', description: '此商品享受退货极速退款服务' }],
			spec: ['成人', '学生'],
			ticket: [], //门票介绍
			selectSpec: null, //选中规格
			isKeep: false, //收藏
			scenicSpotId: '' //景点ID
		};
	},
	onLoad(option) {

		//小程序隐藏返回按钮
		this.showBack = false;

		//option为object类型，会序列化上个页面传递的参数
		this.scenicSpotId = option.id;
		let that = this;
		uni.request({
			url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/scenicSpot/by/id/',
			method: 'GET',
			data: { id: that.scenicSpotId },
			success: res => {
				that.img = res.data.data.img;
				that.name = res.data.data.scenicSpotName;
				that.Address = res.data.data.scenicSpotAddress;
				that.JDescribe = res.data.data.scenicSpotDescribe;
				that.Describe = res.data.data.scenicSpotSynopsis;
				that.swiperList = res.data.data.scenicSpotImgsListBos;
				that.adultTicketPrice = res.data.data.adultTicketPrice;
				that.childrenTicketPrice = res.data.data.childrenTicketPrice;
				that.ticketDescribe = res.data.data.ticketDescribe;
				that.ticketName = res.data.data.ticketName;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onReady() {
		this.calcAnchor(); //计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	methods: {
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		// 分享
		share() {
			this.shareClass = 'show';
		},
		hideShare() {
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		//地图
		goodMap() {
			uni.navigateTo({
				url: '/pages/details/goodMap?id=' + this.scenicSpotId
			});
			uni.setStorage({
				key: 'maps_id',
				data: this.scenicSpotId,
				success: function() {
					console.log('successmaps_id');
				}
			});
		},
		//收藏
		keep() {
			this.isKeep = this.isKeep ? false : true;
			var that = this;

			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (!res.confirm && this.isKeep) {
							that.isKeep = false;
						} else {
							that.isKeep = false;
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				});
			} else {
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/insert/favorite',
					method: 'POST',
					data: {
						userId: this.userId,
						scenicSpotId: this.scenicSpotId
					},
					success: res => {
						uni.showToast({
							icon: 'null',
							title: '收藏成功'
						});
						that.isKeep = true;
						setTimeout(() => {
							uni.hideToast();
						}, 500);
					},
					fail: () => {
						that.isKeep = false;
					}
				});
			}
		},

		joinCart() {
			let that = this;
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (!res.confirm) {
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				});
			} else {
				if (this.selectSpec == null) {
					return this.showSpec(() => {
						uni.request({
							url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/shopping/car', //添加购物车
							method: 'POST',
							data: {
								number: that.number,
								spec: '规格:' + that.spec[that.selectSpec],
								ticketId: that.scenicSpotId,
								userId: that.userId
							},
							success: res => {
								uni.showToast({ title: res.data.msg });
								setTimeout(() => {
									uni.hideToast();
								}, 500);
							}
						});
					});
				} else {
					console.log(that);
					uni.request({
						url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/shopping/car', //添加购物车
						method: 'POST',
						data: {
							number: that.number,
							spec: '规格:' + that.spec[that.selectSpec],
							ticketId: that.scenicSpotId,
							userId: that.userId
						},
						success: res => {
							uni.showToast({ title: res.data.msg });
							setTimeout(() => {
								uni.hideToast();
							}, 500);
						},
						fail: res => {
							uni.showToast({ title: res.data.msg });
							setTimeout(() => {
								uni.hideToast();
							}, 500);
						}
					});
				}
			}
		},
		//立即购买
		buy() {
			let that = this;
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (!res.confirm) {
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				});
			} else {
				if (this.selectSpec == null) {
					return this.showSpec(() => {
						this.toConfirmation();
					});
				}
				this.toConfirmation();
			}
		},
		//商品评论

		toRatings() {
			uni.navigateTo({
				url: '../ratings/ratings?id=' + this.scenicSpotId
			});
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let paymentOrder = [];
			let goods = {
				scenicSpotId: this.scenicSpotId,
				img: this.img,
				name: this.name,
				spec: '规格:' + this.spec[this.selectSpec],
				adultTicketPrice: this.adultTicketPrice,
				childrenTicketPrice: this.childrenTicketPrice,
				number: this.number
			};
			console.log(this.scenicSpotId);
			let order = {
				ticketId: this.scenicSpotId,
				spec: '规格:' + this.spec[this.selectSpec],
				number: this.number
			};
			tmpList.push(goods);
			paymentOrder.push(order);
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../order/confirmation?buylist=' + tmpList
					});
				}
			});
			uni.setStorage({
				key: 'payment_order',
				data: paymentOrder
			});
		},
		//跳转评论列表
		showComments(goodsid) {},
		//提交评论
		submissionComment() {
			let that = this;
			if (!this.hasLogin) {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					/**
					 * 如果需要强制登录，不显示取消按钮
					 */
					showCancel: !this.forcedLogin,
					success: res => {
						if (!res.confirm) {
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				});
			} else {
				if (that.mycomment.length == 0) {
					uni.showToast({
						title: '什么都没写'
					});
					setTimeout(() => {
						uni.hideToast();
					}, 500);
				} else {
					uni.request({
						url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/insert/comment',
						method: 'POST',
						data: {
							comment: that.mycomment,
							scenicSpotId: that.scenicSpotId,
							userId: that.userId
						},
						success: res => {
							uni.showToast({
								title: '评论成功'
							});
							setTimeout(() => {
								uni.hideToast();
							}, 500);
						},
						fail() {}
					});
				}
			}
		},
		//选择规格
		setSelectSpec(index) {
			this.selectSpec = index;
		},
		//减少数量
		sub() {
			if (this.number <= 1) {
				return;
			}
			this.number--;
		},
		//增加数量
		add() {
			this.number++;
		},
		
		
		//服务弹窗
		showService() {
			console.log('show');
			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback() {
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec && this.specCallback && this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {
			//丢弃
		}
	}
};
